import React, { Component } from "react";
import { useState } from "react";
import Axios from "axios";
import Home from "./Home";
import { createContext } from "react";
import {useContext} from 'react'
import {globalinfo} from '../App.js'
import { useCookies } from "react-cookie";
import './Signup.css'


function Signup() {
  const {setff,setll,setemaill}= useContext(globalinfo)
  const { setCookiee} = useContext(globalinfo);
      const [firstname,setf]=useState("")
  const [lastname,setl]=useState("")
  const [email,sete]=useState("")
  const [password,setp]=useState("")
  const {setresc}=useContext(globalinfo);
  const {resc}=useContext(globalinfo);
const url="https://dontbeshitafraid.herokuapp.com/testapi/signup"

  function handlef(e){
  
    const newdata={...firstname};
    newdata[e.target.id]=e.target.value;
    setf(newdata)
    
// console.log(firstname.inputf,"vkjv")
 


     
// setq(h.textContent)
    
}

function handlel(e){
  
    const newdata={...lastname};
    newdata[e.target.id]=e.target.value;
    setl(newdata)
    
// console.log(lastname.inputl,"vkjv")
 


     
// setq(h.textContent)
    
}

function handlee(e){
  
    const newdata={...email};
    newdata[e.target.id]=e.target.value;
    sete(newdata)
    
// console.log(email.inpute,"vkjv")
 


     
// setq(h.textContent)
    
}

function handlep(e){
  
    const newdata={...password};
    newdata[e.target.id]=e.target.value;
    setp(newdata)
    
// console.log(password.inputp,"vkjv")
 


     
// setq(h.textContent)
    
}
let axiosConfig = {
  withCredentials: true
}

     function submit(e){

       setresc(1)
        e.preventDefault(); 
        if((firstname).length===0 ||(lastname).length===0 || (email).length===0 || (password).length===0 )
        {
            window.confirm("Please Fill Properly")
        }
        else{
          // Axios.defaults.withCredentials = true
        //   const requestOptions = {
        //     method: 'POST',
        //     headers: { 'Content-Type': 'application/json' },
        //     body:JSON.stringify( {  firstname:firstname.inputf,
        //       lastname:lastname.inputl,
        //       email:email.inpute,
        //       password:password.inputp })
        // };
          Axios.post(url,{
        
        
               firstname:firstname.inputf,
               lastname:lastname.inputl,
               email:email.inpute,
               password:password.inputp

          })
        .then(res=>{
           console.log(res.data[0]);
          
           setresc((parseInt( res.data[0])));
           setCookiee("user",res.data[1], { path: '/', maxAge: 31536000 });
           setff(firstname.inputf)
           setll(lastname.inputl)
           setemaill(email.inpute)
            })
        }// else

     }
    

     if(resc===0  )
{
  

 
  // console.log("dwqhgduiqhdwqdiwqodhwqoih"+cookies.user)
  return(<Home/>)
}
  else{
  
        return (
            <form onSubmit={(e)=>submit(e,)}>
        <h3>Sign up</h3>
        <div className="mb-3">
          <label>First name</label>
          <input
            type="text"
            className="form-control" id="inputf"
            placeholder="Enter your first name" onChange={(e)=>handlef(e)}
          />
        </div>
        <div className="mb-3">
          <label>Last name</label>
          <input onChange={(e)=>handlel(e)}
            type="text"
            className="form-control"
            placeholder="Enter your last name" id="inputl"
          />
        </div>
        <div className="mb-3">
          <label>Email address</label>
          <input 
            type="email"
            className="form-control"
            placeholder="Enter email" id="inpute" onChange={(e)=>handlee(e)}
          />
        </div>
        <div className="mb-3">
          <label>Password</label>
          <input 
            type="password"
            className="form-control"
            placeholder="Enter password" id="inputp" onChange={(e)=>handlep(e)}
          />
        </div>
        
        <div className="d-grid">
          <button type="submit" className="btn btn-primary">
            Sign up
          </button>
        </div>
       
      </form>
        );
    }
  }

export default Signup