import React, { Component } from "react";
import { useState } from "react";
import Axios from "axios";
import { useEffect } from "react";
import Home from "./Home";
import {useContext} from 'react'
import {globalinfo} from '../App.js'
function Signup() {
  const { setCookiee} = useContext(globalinfo);
  const {setff,setll,setemaill}= useContext(globalinfo);
  const [Firstname,setf]=useState("")
  const [Lastname,setlast]=useState("")
  const [email,sete]=useState("")
  const [password,setp]=useState("")
  const [loading,setl]=useState(false)
  const {resc}=useContext(globalinfo);
  const {setresc}=useContext(globalinfo);
  const [lresc,setlresc]=useState("");
  // const [resc,setresc]=useState("")

const url="https://dontbeshitafraid.herokuapp.com/testapi/login"

 

function handlee(e){
  
    const newdata={...email};
    newdata[e.target.id]=e.target.value;
    sete(newdata)
    
console.log(email.inpute,"vkjv")
 


     
// setq(h.textContent)
    
}

function handlep(e){
  
    const newdata={...password};
    newdata[e.target.id]=e.target.value;
    setp(newdata)
    
console.log(password.inputp,"vkjv")
 


     
// setq(h.textContent)
    
}

     function submit(e){

      
        e.preventDefault(); 
        if( (email).length===0 || (password).length===0 )
        {
            window.confirm("Please Fill Properly")
        }
        else{
             Axios.post(url,{
        
        
              
               email:email.inpute,
               password:password.inputp
            }).then(res=>{
        
              setlresc(res.data[0])
              console.log("hi "+lresc);
              console.log(res.data[1] + res.data[2])
setf(res.data[1]);
setlast(res.data[2]);
setff(res.data[1])
  setll(res.data[2])
  setemaill(email.inpute)
 
  setCookiee("user",res.data[3], { path: '/', maxAge: 31536000 });
console.log(resc)
                
                 
            })

            console.log("loading")
           
           setl(true)

            
        }



     }



if(resc===0 || lresc==1)
{

  
  return(<Home/>)
}
  
        return (
            <form onSubmit={(e)=>submit(e)}>
        <h3>Sign In</h3>
        
        <div className="mb-3">
          <label>Email address</label>
          <input 
            type="email"
            className="form-control"
            placeholder="Enter email" id="inpute" onChange={(e)=>handlee(e)}
          />
        </div>
        <div className="mb-3">
          <label>Password</label>
          <input 
            type="password"
            className="form-control"
            placeholder="Enter password" id="inputp" onChange={(e)=>handlep(e)}
          />
        </div>
        
        <div className="d-grid">
          <button type="submit" className="btn btn-primary">
            Log in 
          </button>
        </div>
       
      </form>
        );
        
        }

export default Signup