import React, { useContext, useEffect, useState } from 'react'
import './Profile.css'
import trex from './7IXT.gif'

import {globalinfo} from '../App.js'
function Pquestions() {


const {eemail}=useContext(globalinfo);
console.log(eemail)
const [Pquestions,Psetquestions]=useState([]);

const url=`https://dontbeshitafraid.herokuapp.com/testapi/showq?email=${eemail}`;


  fetch(url)
  .then(results=>results.json())
  .then(data=>{
    Psetquestions(data)
   
  })


  



  return (
    <div className='Parent'>
  
    <div className='Child2'>

<div className='Child2-2'>
{
  eemail=="Guest"?<h5>Questions</h5>:
  <h5>Questions {Pquestions.length}</h5>}

{ 

 eemail=='Guest'?<p>Log in now </p> :

Pquestions.map((item)=>(

    <p>{item.question}</p>
    
    
))

}


<img src={trex}></img>
</div>

    </div>

</div>
  )
}

export default Pquestions