import React, { useEffect } from 'react'
import './Profile.css'
import pikachu from './5IPv.gif'
import {globalinfo} from '../App.js'
import { useContext } from 'react'
import { useState } from 'react'
function Panswers() {

  const {eemail}=useContext(globalinfo);
  const [Panswers,Psetanswers]=useState([]);

  useEffect(()=>{
  fetch(`https://dontbeshitafraid.herokuapp.com/testapi/Panswers?email=${eemail}`)
  .then(res=>res.json())
  .then(data=>{
    Psetanswers(data)
  })
  },[])

  console.log(Panswers)
  return (



    <div className='Parent'>
  
    <div className='Child2'>

<div className='Child2-2'>

{
  eemail=="Guest"?<h5>Answers</h5>:
  <h5>Answers {Panswers.length}</h5>
  }


{ 

eemail=='Guest'?<p>Log in now </p> :

Panswers.map((item)=>(
<>
  
   <p className='Panswersp'>{item.question}</p>
<span>{item.answer}</span>
   </>
))

}







<img className='Panswerimage' src={pikachu}></img>
</div>

    </div>

</div>
  )
}

export default Panswers